$(document).ready(function () {
    $('.owl-carousel').owlCarousel({
        loop: true,
        margin: 30,
        items: 3,
        smartSpeed: 500,
        dots: true,
        autoplay: true,
        autoplayTimeout: 3000,
        autoplayHoverPause: true
    });

    var wow = new WOW({offset: 80});
    wow.init();

    // new WOW({}).init({offset: 80});

    $.fn.parallax = function (resistance, mouse) {
        $el = $(this);
        TweenLite.to($el, 0.2,
            {
                x: -((mouse.clientX - (window.innerWidth / 2)) / resistance),
                y: -((mouse.clientY - (window.innerHeight / 2)) / resistance)
            });
    };

    $.fn.parallaxY = function (resistance, mouse) {
        $el = $(this);
        TweenLite.to($el, 0.2,
            {
                y: -((mouse.clientY - (window.innerHeight / 2)) / resistance)
            });
    };

    $('body').mousemove(function (e) {
        $('.bg__bear_red').parallaxY(-10, e);
        $('.bg__bear_violet').parallax(10, e);
        $('.header__images-bouquet').parallax(-50, e);
        $('.header__images-bouquet-shadow').parallax(-50, e);
        $('.header__images-blur-flower').parallax(40, e);
        $('.header__images-left-leaf').parallax(-20, e);
        $('.header__images-left-fl').parallax(30, e);
        $('.header__images-right-fl').parallax(-20, e);
        $('.header__images-blur-leaf').parallax(10, e);
        $('.header__images-right-leaf').parallax(-20, e);
    });

    var tabs = document.querySelectorAll(".goods__list-tab");

    tabs.forEach(function (btn) {
        btn.addEventListener("click", function () {
            var i, tabcontent, tablinks;
            var id = this.getAttribute("data-id");
            tabcontent = document.getElementsByClassName("goods__item-wrap");
            for (i = 0; i < tabcontent.length; i++) {
                tabcontent[i].style.display = "none";
            }
            tablinks = document.getElementsByClassName("goods__list-tab");
            for (i = 0; i < tablinks.length; i++) {
                tablinks[i].className = tablinks[i].className.replace(" goods__list-tab-active", "");
            }
            document.getElementById(id).style.display = "grid";
            this.className += " goods__list-tab-active";
        })
    });
    document.getElementById("defaultOpen").click();

    $(".main__menu-link").on("click", function () {
        var id = $(this).attr('href'),
            top = $(id).offset().top;
        $('body,html').animate({scrollTop: top});
    });


    var modal = document.querySelector('.modal-form');
    var overlay = document.querySelector('.overlay');
    var btns = document.querySelectorAll(".openModal");
    var closModal = document.querySelector(".close-modal");

    btns.forEach(function (btn) {
        btn.addEventListener("click", function () {
            overlay.style.display = "block";
            modal.style.display = "block"
            modal.classList.toggle("modal-active");
            overlay.classList.toggle("overlay-active");
        })
    });

    closModal.addEventListener("click", function () {
        overlay.style.display = "none";
        modal.style.display = "none";
        modal.classList.toggle("modal-active");
        overlay.classList.toggle("overlay-active");
    });

    overlay.addEventListener("click", function () {
        overlay.style.display = "none";
        modal.style.display = "none";
        modal.classList.toggle("modal-active");
        overlay.classList.toggle("overlay-active");
    })

});